body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#toast {
    border-radius: 4px;
    border-width: 1px;
    font-size: 13px;
    line-height: 19.5px;
    margin: 0px 0px 16px;
    padding: 12px 59.5px 12px 20px;
    text-align: left;
    width: 300px;
    min-height: 80px;
    position: fixed;
    top: 10px;
    right: -320px;
    opacity: 0;
    transition: all 0.5s;
  }

  #toast.success {
    opacity: 1;
    right: 10px;
    background-color: hsl(94, 40%, 86%);
    color: hsl(94, 90%, 16%);
    transition: all 1.2s;
  }

